.hero {
  position: relative;
  background-color: $white;

  $block-name:                &; // #{$block-name}__element

  &__img {
    min-height: 150px;
    //max-height: 960px;
    background-color: $main-color;
    overflow: hidden;

    img,
    video {
      display: block;
      min-width: 100%;
      min-height: 100%;
      object-fit: cover;
      object-position: center;
      border: none;
    }
  }

  &__title-wrapper {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
  }

  &__title {
    margin: 0;
  }

  &__logo-overlay {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      display: block;
      width: 70%;
    }
  }
}
